"use client";

import { useState, useEffect } from "react";

export default function EnderecoAutoComplete() {
  const [query, setQuery] = useState("");
  const [suggestions, setSuggestions] = useState([]);

  useEffect(() => {
    if (query.length < 3) {
      setSuggestions([]);
      return;
    }

    const timeout = setTimeout(() => {
      fetch(`https://photon.komoot.io/api/?q=${encodeURIComponent(query)}&limit=5`)
        .then(res => res.json())
        .then(data => {
          setSuggestions(data.features || []);
        })
        .catch(err => {
          console.error("Erro ao buscar sugestões:", err);
          setSuggestions([]);
        });
    }, 300);

    return () => clearTimeout(timeout);
  }, [query]);

  const handleSelect = (suggestion) => {
    setQuery(suggestion.properties.name + ", " + suggestion.properties.city);
    setSuggestions([]);
  };

  return (
    <div className="mb-3 position-relative">
      <label htmlFor="addrs-foco" className="form-label">Endereço do foco: *</label>
      <input
        type="text"
        className="form-control"
        id="addrs-foco"
        name="addrs-foco"
        required
        autoComplete="off"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
      />
      {suggestions.length > 0 && (
        <div className="list-group position-absolute w-100 z-3" style={{ maxHeight: 200, overflowY: "auto" }}>
          {suggestions.map((sug, i) => (
            <button
              key={i}
              type="button"
              className="list-group-item list-group-item-action"
              onClick={() => handleSelect(sug)}
            >
              {sug.properties.name}, {sug.properties.city}, {sug.properties.country}
            </button>
          ))}
        </div>
      )}
    </div>
  );
}
